* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Mulish;
}

html {
  font-size: 55.5%;
}

i {
  width: 2rem;
  height: 2rem;
}

.fa,
.fas {
  font-weight: 900;
  display: grid;
  place-items: center;
  font-size: 2.6rem;
  cursor: pointer;
}
.hide{display: none;}
:root {
    --main-color: #333;
    --primary-color: #acacac;
    --icon-color: #525252;
    --light-grey: #5c6f7f;
    --card-text : #354052;
    --heading-color : #47e0e2;
}
  
body {
  width: 80%;
  height: 100vh;
  margin: auto;
}

header {
  width: 100%;
  line-height: 10rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  border-bottom: 0.1rem solid #9a9191;
  -webkit-transform: rotate(-0.05deg);
          transform: rotate(-0.05deg);
  margin-bottom: 7rem;
}
.wiggles-header-logo a,
.page-heading {
    text-align: left;
    color: #47e0e2;
    text-decoration: none;
}
.wiggles-header-logo a h1{
  font-size: 3rem;
}
.continue-shopping {
  display: -webkit-flex;
  display: flex;
  height: inherit;
  -webkit-align-items: center;
          align-items: center;
}

.continue-shopping h3 {
  font-size: 2rem;
  text-transform: capitalize;
  color: #333;
  color: var(--main-color);
  margin-left: 1.5rem;
  font-weight: 400;
}

.cart-icon {
  display: -webkit-flex;
  display: flex;
  text-align: end;
  margin-right: 2rem;
  -webkit-align-items: center;
          align-items: center;
  position: relative;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.cart-icon i {
  color: #525252;
  color: var(--icon-color);
}

.cart-icon p {
    position: absolute;
    width: 2.5rem;
    height: 2.5rem;
    right: -2.2rem;
    top: 2.3rem;
    border-radius: 50%;
    background: #47e0e2;
    color: #fff;
    box-sizing: border-box;
    font-size: 1.3rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    color: #333;
    color: var(--main-color);
}
.main-cart-section{
    text-align: left;
}
.main-cart-section .sub-heading{
  margin-bottom: 30px;
}
.main-cart-section h1 {
  font-style: normal;
  font-weight: bold;
  font-size: 2.3rem;
  text-transform: capitalize;
  color: #47e0e2;
  color: var(--heading-color);
  margin-bottom: 1rem;
}
.main-cart-section .detail-view h1{
  color:#354052;
  color:var(--card-text);
  font-size: 3rem;
}
.main-cart-section .detail-view *{
  font-size: 2.3rem;
}
.main-cart-section h1.pro-heading small{
  float: right;
}
.main-cart-section h1.pro-heading small i{
  display: inherit;
}

.main-cart-section .total-items {
  font-style: normal;
  font-weight: normal;
  font-size: 1.8rem;
  text-transform: capitalize;
  color: #333;
  color: var(--main-color);
  margin-bottom: 5rem;
}

.total-items-count {
  font-weight: bold;
  color: #333;
  color: var(--main-color);
}

/* cart main div start  */

.main-cart-section .cart-items {
  width: 100%;
  height: 62rem;
  background: linear-gradient(
    103.49deg,
    #ffffff -28.13%,
    rgba(242, 247, 255, 0.5) 116.84%
  );
  box-shadow: rgba(0, 0, 0, 0.08) 0rem 0.4rem 1.2rem;
  border-radius: 2rem;
  margin: auto;
  margin: auto;
  display: grid;
  place-items: center;
}

.cart-items hr {
  margin: 2rem auto;
  overflow-y: scroll;
}

.cart-items-container {
  width: 90%;
  height: 56rem;
}

.items-info {
  width: 100%;
  height: 11rem;
  /* background-color: lavender; */
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}

.items-info .product-img img {
  width: 16rem;
  height: 11rem;
  -webkit-filter: drop-shadow(0rem 0.4rem 1rem #f1f7ff);
          filter: drop-shadow(0rem 0.4rem 1rem #f1f7ff);
  border-radius: 1rem;
}

.items-info .title {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.items-info .title h2 {
  font-style: normal;
  font-weight: bold;
  font-size: 2.2rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  text-transform: capitalize;
  color: #333;
  color: var(--main-color);
}

.items-info .title p {
  font-style: normal;
  font-weight: normal;
  font-size: 1.8rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  text-transform: capitalize;
  text-align: left;
  color: #acacac;
  color: var(--primary-color);
}

.add-minus-quantity {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
}

.add-minus-quantity button {
  border: none;
  background-color: transparent;
  outline: none;
  cursor: pointer;
}

.add-minus-quantity input {
  width: 6rem;
  height: 3rem;
  border: 0.141rem solid #acacac;
  border: 0.141rem solid var(--primary-color);
  box-sizing: border-box;
  font-style: normal;
  font-weight: normal;
  font-size: 1.6rem;
  text-align: center;
  text-transform: capitalize;
  color: #acacac;
  color: var(--primary-color);
  margin: 0 1rem;
  border-radius: 0.5rem;
  outline: none;
  background-color: transparent;
}

.add-minus-quantity input:focus {
  outline: none;
}

.price {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
}

.price h3 {
  font-style: normal;
  font-weight: bold;
  font-size: 2rem;
  text-transform: capitalize;
  color: #333;
  color: var(--main-color);
}

.remove-item {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
  margin-right: 5rem;
}

.remove-item button {
  border: none;
  background-color: transparent;
}

.card-total {
  width: 95%;
  margin-top: 4rem;
  text-align: right;
}

.card-total h3 {
  font-style: 200;
  font-size: 2.58rem;
  line-height: 3.2rem;
  text-transform: capitalize;
  color: #606166;
}

.card-total h3 span {
  font-style: normal;
  font-weight: bold;
  font-size: 2.8rem;
  line-height: 3.2rem;
  text-transform: capitalize;
  color: #000000;
  margin-left: 1rem;
}

.card-total button {
  border: none;
  font-size: 1.6rem;
  padding: 1rem 3rem;
  color: #fff;
  background-color: #349bf3;
  text-transform: uppercase;
  margin-top: 1rem;
  border-radius: 0.5rem;
  cursor: pointer;
  letter-spacing: 1px;
}

/* hover effects  */
.fa-trash-alt {
  color: #ed4848;
}

.fa-plus:hover {
  color: #42c157;
}

.fa-minus:hover {
  color: #ffb800;
}

.fa-plus,
.fa-minus {
  color: #525252;
  color: var(--icon-color);
}

.card-total button:hover {
  background-color: #0077dc;
}

.card-total .clear-cart {
  margin-left: 3rem;
  background-color: rgb(209, 61, 61);
}

.card-total .clear-cart:hover {
  background-color: rgb(197, 5, 5);
}

.text-danger{
  color:red
}
.btn{
    display: inline-block;
    font-weight: 400;
    color: #212529;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    -webkit-user-select: none;
    -ms-user-select: none;
        user-select: none;
    background-color: transparent;
    border: 1px solid transparent;
    padding: .85714rem 1rem;
    font-size: 1.5rem;
    line-height: 1.5;
    display: block;
    margin: 0 auto;
    border-radius: 0;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}
.btn-outline-primary{
    color: #4f2ca2;
    border-color: #4f2ca2;
}
.btn-primary{
    color: #fff;
    border-color: #4f2ca2;
    background-color: #4f2ca2;

}
footer {
    width: 100%;
    line-height: 10rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    border-top: 0.1rem solid #9a9191;
    -webkit-transform: rotate(
-0.05deg
);
            transform: rotate(
-0.05deg
);
    margin-top: 7rem;
    text-align: left;
}
.remove-item {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
  margin-right: 5rem;
}

.remove-item button {
  border: none;
  background-color: transparent;
}

.detail-view div:first-of-type{
    -webkit-flex-grow: 0;
            flex-grow: 0;
    display:inline-block;
}
.detail-view div:last-of-type{
  -webkit-flex-grow: 1000;
          flex-grow: 1000;
  display:inline-block;
  text-align: left;
}

.pro-heading h1 small{
  float: right;
}
.pro-heading small a{
  color: #47e0e2;
  color: var(--heading-color);
  text-decoration: none;
}
.product-listing .nav-buttons button,
.product-listing .nav-buttons a.btn{
  display: inline-block;
  margin:5px;
  text-decoration: none;
  border-color: #4f2ca2;
}
.main-cart-section .detail-view img{
  width:100%;
  max-width: 100%;
}
.main-cart-section .detail-view div{
  background: transparent
}
/* responsive media queries  */

@media (max-width: 968px) {
  html {
    font-size: 50%;
  }
}

@media (max-width: 768px) {
  .continue-shopping h3 {
    margin-left: 0;
    font-size: 1.85rem;
  }

  .items-info {
    width: 100%;
    height: auto;
    display: grid;
    grid-template-columns: 1fr;
  }

  .title,
  .add-minus-quantity,
  .price,
  .remove-item {
    padding-left: 2rem;
  }

  .items-info .product-img {
    width: 100%;
    text-align: center;
    margin-bottom: 2rem;
  }

  .add-minus-quantity {
    margin: 2rem 0 2rem 0;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
  }

  .price {
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    margin-bottom: 2rem;
  }

  .price h3::before {
    content: "Price: ";
  }

  .remove-item {
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
  }

  .card-total {
    margin-bottom: 2rem;
    text-align: center;
  }

  .card-total button {
    margin-bottom: 3rem;
  }
}
